<template>
	<div>
		<div v-if="books.length" class="books">
			<table>
				<thead>
					<th>id</th>
					<th>书籍名称</th>
					<th>出版日期</th>
					<th>价格</th>
					<th>购买数量</th>
					<th>操作</th>
				</thead>
				<tbody>
					<tr v-for="(item, index) in books" :key="item.id">
						<td>{{ item.id }}</td>
						<td>{{ item.name }}</td>
						<td>{{ item.date }}</td>
						<td>{{ item.price | filt }}</td>
						<td>
							<button @click="sub(index)" :disabled="item.count <= 1">-</button>
							{{ item.count }}
							<button @click="add(index)">+</button>
						</td>
						<td><button @click="booksitem(index)">移除</button></td>
					</tr>
				</tbody>
			</table>
			<div class="sum">总价格:{{ sums | filt }}</div>
		</div>
		<div v-else class="gouwuche">购物车为空</div>
	</div>
</template>

<script>
export default {
	name: 'books',
	components: {},
	mixins: [],
	props: {},
	data() {
		return {
			books: [
				{
					id: 1,
					name: '算法',
					date: '2022-3-11',
					price: 85.0,
					count: 1,
				},
				{
					id: 2,
					name: '算法底层',
					date: '2022-3-11',
					price: 85.0,
					count: 1,
				},
				{
					id: 3,
					name: '历史',
					date: '2022-3-11',
					price: 60.0,
					count: 1,
				},
				{
					id: 4,
					name: '哲学',
					date: '2022-3-11',
					price: 70.0,
					count: 1,
				},
			],
		}
	},
	computed: {
     sums() {
          /* let sum = 0;
           for (let val in this.books) {
              sum += this.books[val].price * this.books[val].count
           }*/
        return  this.books.reduce(function (pretvalue, item) {
         return pretvalue + item.price * item.count;
        }, 0)               //reduce()方法把里面的值自相加
     }          
  },
	watch: {},
	created() {},
	mounted() {},
	destroyed() {},
	methods: {
		add(index) {
			this.books[index].count++
		},
		sub(index) {
			this.books[index].count--
		},
		//移除当前数据
		booksitem(index) {
			this.books.splice(index, 1)
		},
	},
   filters: {
    filt(price) {
        //toFixed(2)保留两位小数
        return '￥' + price.toFixed(2);
        }
     }
}
</script>
<style lang='less' scoped>
  .books table{
    margin: 0 auto;
  }
  .books table tr td {
    width: 80px;
  }
   .books table tr td button {
     padding: 5px;
   }
   .books table th,.books table tr td {
     text-align: center;
   }
  .books .sum,.books .gouwuche {
    text-align: center;
     margin-left: -280px;
  }
</style>
